<template>
	<view>
		<view class="item" v-for="(res, index) in siteList" :key="res.id">
			<view class="top">
				<view class="name">{{ res.name }}</view>
				<view class="phone">{{ hiddenPhone(res.phone) }}</view>
				<view class="tag">
					<text :class="{red:res.is_default==1}">{{res.is_default==1?' 默认 ':' 狗窝 '}}</text>
				</view>
			</view>
			<view class="bottom">
				{{res.province+res.city+res.county+' '+res.address}}
				<!-- <view class="icon"> -->
					<!-- <u-icon name="trash" :size="40" color="#999999"></u-icon> -->
					
					<u-icon name="edit-pen" :size="40" color="#999999" @click='editAddress(res.id)'></u-icon>
				<!-- </view> -->
			</view>
		</view>
		<view class="addSite" @tap="toAddSite">
			<view class="add">
				<u-icon name="plus" color="#ffffff" class="icon" :size="30"></u-icon>新建收货地址
			</view>
		</view>
	</view>
</template>

<script>
import user from '../../utils/user.js'
export default {
	data() {
		return {
			siteList: [],
			hiddenPhone:user.hiddenPhone
		};
	},
	onLoad() {
		// this.getData();
		this.getAddressList()
	},
	onShow() {
		this.getAddressList()
	},
	methods: {
		toAddSite(){
			uni.navigateTo({
			    url: '/pages/address/addSite'
			});
		},
		async getAddressList(){
			const result=await this.$u.api.getAddressList()
			this.siteList=result.data
			console.log(result)
		},
		editAddress(id){
			if(id){
				console.log(id)
				this.$u.route({
					url:`/pages/address/addSite?id=`+id
				})
			}else{
				uni.showToast({
					title:'error,请重试'
				})
			}
		}
	}
};
</script>

<style lang="scss" scoped>
.item {
	padding: 40rpx 20rpx;
	.top {
		display: flex;
		font-weight: bold;
		font-size: 34rpx;
		.phone {
			margin-left: 60rpx;
		}
		.tag {
			display: flex;
			font-weight: normal;
			align-items: center;
			text {
				display: block;
				width: 60rpx;
				height: 34rpx;
				line-height: 34rpx;
				color: #ffffff;
				font-size: 20rpx;
				border-radius: 6rpx;
				text-align: center;
				margin-left: 30rpx;
				background-color:rgb(49, 145, 253);
			}
			.red{
				background-color:red
			}
		}
	}
	.bottom {
		display: flex;
		margin-top: 20rpx;
		font-size: 28rpx;
		justify-content: space-between;
		color: #999999;
		.icon{
			display: flex;
			justify-content: space-between;
		}
	}
}
.addSite {
	display: flex;
	justify-content: space-around;
	width: 600rpx;
	line-height: 100rpx;
	position: absolute;
	bottom: 30rpx;
	left: 80rpx;
	background-color: red;
	border-radius: 60rpx;
	font-size: 30rpx;
	.add{
		display: flex;
		align-items: center;
		color: #ffffff;
		.icon{
			margin-right: 10rpx;
		}
	}
}
</style>
